import { Link } from 'react-router-dom';

function SiteMap() {

	const arrLinks = [
		{
			name : '',
            link : '/',
        },
        {
			name : '',
            link : '/profile',
        },
        {
			name : ' -  ',
            link : '/profile/verification',
        },
        {
			name : ' - ',
            link : '/profile/conclusions',
        },
        {
			name : ' -  ',
            link : '/profile/betting',
        },
        {
			name : ' -  ',
            link : '/profile/promo',
        },
        {
			name : ' -  ',
            link : '/profile/deposit',
        },
        {
			name : '',
            link : '/events',
        },
        {
            name: ' - ',
            link: '/promotions',
        },
        {
            name: ' - ',
            link: '/promotions/promo',
        },
        {
            name: '',
            link: '/apps',
        },
        {
			name : '',
            link : '/statistics',
        },
        {
			name : ' - Big V 2023',
            link : '/statistics/big-v-2023',
        },
        {
			name : ' - ',
            link : '/statistics/calendar',
        },
        {
			name : ' - ',
            link : '/statistics/table',
        },
        {
            name: '',
            link: '/cybersport',
        },
        {
            name: '  - ,   ,   ',
            link: '/profile/return',
        },
        {
            name: ' - ',
            link: '/statistics/detailed',
        },
        {
            name: ' - ',
            link: '/statistics/face-to-face',
        },
        {
            name: ' -  ',
            link: '/statistics/standings',
        },
        {
            name: '  ',
            link: '/choice-of-sport',
        },
        {
            name: '  ',
            link: '/faq',
        },
        {
            name: '',
            link: '/requests'
        },
	]

	return (
		<ul className="sitemap">
            {arrLinks.map((item, index) => (
            <li key={index} className="sitemap__item">
                <Link to={item.link} className="sitemap__link">{item.name}</Link>
            </li>
            ))}
		</ul>
	);
}

export default SiteMap;