import clsx from 'clsx';

function Requests() {

	const arrRequest = [];
	
	return (
		<div className="requests">
			<div className="requests__wrapper">
				<div className="requests__header">
					<h1 className="requests__header-title"></h1>
					<button className="requests__header-btn">+  </button>
				</div>
				<div className={clsx(
						'requests__content',
						{'requests__content--empty': arrRequest.length === 0}
					)}>
					{ (arrRequest.length === 0) ? (
						<div className="requests__empty">   </div>
					) : null }
				</div>
			</div>
		</div>
	);
}

export default Requests;